
.register {
  width: 100%;
  overflow-x: hidden;
  text-align: center;
}
.register .login-bg {
  position: fixed;
  top: 3em;
  left: 0;
  z-index: -1;
  background: #478aff;
  width: 100%;
  height: 580px;
}
.register .login-bg img {
  height: 580px;
}
.register .dialog-wrapper {
  margin-top: 150px;
  margin-left: 300px;
  padding: 10px;
  background: rgba(255,255,255,0.3);
}
.register .dialog-wrapper .login-dialog {
  padding: 2em 1em;
  background: #fff;
  border: 1px solid #ccc;
}
.register .dialog-wrapper .login-dialog .login-item {
  margin: 0.5em;
}
.register .dialog-wrapper .login-dialog .login-item input {
  width: 100%;
}
.register .dialog-wrapper .login-dialog .login-item button {
  width: 100%;
  display: inline-block;
}


